@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;700&display=swap);
:root{
  --backColor--:hsl(207, 26%, 17%);
  --textColor--:hsl(200, 15%, 8%);
  --elementsBackColor--:hsl(0, 0%, 100%);
  --inputTextColor--:hsl(0, 0%, 52%);
}
body {
  margin: 0;
  font-family: 'Nunito Sans', sans-serif;
   font-weight: 300 !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: hsl(207, 26%, 17%) !important;
  background-color: var(--backColor--) !important;
  color: hsl(200, 15%, 8%);
  color: var(--textColor--);

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
b{
  font-weight: 600 !important;
}

h5,h3{
  font-weight: 700 !important;
}
header,main,.card,body,input,label{
  transition: all 0.5s ease-in-out !important;
}
.active{
    display: block !important;
}
.header{
    padding: 15px 5px;
    background-color: var(--elementsBackColor--);
    color:var(--textColor--)

}
.header .brand {
    font-size: 20px;
    font-weight: 700 !important;
}
.header .mode{
    cursor: pointer;
    font-weight: 600;
}
.fa-sun{
    color: gold;
    text-shadow: 0 0 5px gold;
}
main{
    padding-top: 50px;
    padding-bottom: 50px;
    position: relative;
}
.container{
    padding-left: 25px !important;
    padding-right: 25px !important;
}
form{ position: relative; }
input[type='text']{
    min-width: 350px;
    padding-left: 40px;
    border: none;
    background-color: var(--elementsBackColor--) !important;
    color: var(--inputTextColor--) !important;
}
label{
    position: absolute;
    z-index: 90;
    top: 8px;
    left: 10px;
    cursor: pointer;
    background-color:transparent;
}
.dropdown-menu,.dropdown-menu >.dropdown-item{ cursor: pointer; }

.dropdown-menu,.dropdown-menu >.dropdown-item,
label,.country-data { color:var(--textColor--) }

.dropdown-menu,.dropdown-menu >.dropdown-item,
.country-data{ background-color: var(--elementsBackColor--)!important}

.my-text-14px{ font-size: 14px; }

.country-data{
    padding: 0 !important;
    cursor: pointer;
    position: relative;
}

@media (min-width: 768px)
{.country-data.col-md-4 {
    flex: 0 0 30.333333%;
    max-width: 30.333333%;
}}
@media (min-width: 992px)
{.country-data.col-lg-3 {
    flex: 0 0 22%;
    max-width: 22%;
}}




.btn-back{
    padding: 3px 30px !important;
 
}

.country-detail{
    padding: 20px  20px 20px 50px!important ;
    color:var(--textColor--)
}
.country-detail .col-md-6:last-child{
    padding-left: 20px;
}
.country-detail .col-md-6>div{
    margin-bottom: 10px;
}

.country-detail .border-country,.btn-back
{
    box-shadow: 0px 0px 5px hsl(209, 23%, 22%);

}
.country-detail .border-country{
   
    padding: 2px 8px;
    margin-left: 10px;
    border-radius: 5px;

}
#error-message{
    font-size:30px !important;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);

}
.overlay-loading{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
}
.overlay-loading .buble-container{
    width: 300px;
    height: 300px;
    transition: -webkit-animation ease-in-out;
    transition: animation ease-in-out;
    transition: animation ease-in-out, -webkit-animation ease-in-out;
    /* animation: rotate 1.5s linear infinite; */
    position: relative;
    
}
@-webkit-keyframes rotate {
0%{
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
}
100%{
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
}
    
}
@keyframes rotate {
0%{
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
}
100%{
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
}
    
}
.overlay-loading .buble-container .buble{
    width: 50px;
    height: 50px;
  
    border-radius: 50%;
    display: inline-block;
    position: absolute;
    transition: -webkit-animation ease-in-out;
    transition: animation ease-in-out;
    transition: animation ease-in-out, -webkit-animation ease-in-out;
    -webkit-animation: 2.5s linear infinite ;
            animation: 2.5s linear infinite ;
}
.overlay-loading .buble-container .buble:nth-child(1){
    top: 0;
    left: 50%;
    margin-left: -25px;
    -webkit-animation-name: buble-1;
            animation-name: buble-1;
    background-color: Red;
}
@-webkit-keyframes buble-1 {
    0%{
        top:0
    }
    50%{
        top: calc(100% - 50px);

    }
    100%{
        top: 0;
    }
    
}
@keyframes buble-1 {
    0%{
        top:0
    }
    50%{
        top: calc(100% - 50px);

    }
    100%{
        top: 0;
    }
    
}
.overlay-loading .buble-container .buble:nth-child(2){
    top:calc(100% - 50px);
    left: 50%;
    margin-left: -25px;
    background-color: olive;
    -webkit-animation-name: buble-2;
            animation-name: buble-2;
}
@-webkit-keyframes buble-2 {
    0%{ top: calc(100% - 50px);}
    50%{  top:0;}
    100%{ top: calc(100% - 50px)}
    
}
@keyframes buble-2 {
    0%{ top: calc(100% - 50px);}
    50%{  top:0;}
    100%{ top: calc(100% - 50px)}
    
}
.overlay-loading .buble-container .buble:nth-child(3){
    top: 50%;
    left: 0;
    margin-top: -25px;
    background-color: lightgreen;
    -webkit-animation-name: buble-3;
            animation-name: buble-3;

}
@-webkit-keyframes buble-3 {
    0%{
        left:0
    }
    50%{
        left: calc(100% - 50px);

    }
    100%{
        left: 0;
    }
    
}
@keyframes buble-3 {
    0%{
        left:0
    }
    50%{
        left: calc(100% - 50px);

    }
    100%{
        left: 0;
    }
    
}
.overlay-loading .buble-container .buble:nth-child(4){
    top: 50%;
    left: calc(100% - 50px);
    margin-top: -25px;
    background-color: blue;
    -webkit-animation-name: buble-4;
            animation-name: buble-4;

}
@-webkit-keyframes buble-4 {
    0%{
        left:calc(100% - 50px)
    }
    50%{
        left: 0;

    }
    100%{
        left: calc(100% - 50px);
    }
    
}
@keyframes buble-4 {
    0%{
        left:calc(100% - 50px)
    }
    50%{
        left: 0;

    }
    100%{
        left: calc(100% - 50px);
    }
    
}

#mapId{ height: 500px; }
.my-text-16px{ font-size: 16px; }
